import { Component } from 'react'
import Layout from './../components/MyLayout'
import Cadre from './../components/Cadre'

export default class extends Component {
  /*static async getInitialProps(){
  	const response = await fetch(`https://api.jikan.moe/v3/season/${(new Date()).getYear() + 1900}/${getSeason()}`)
    const data = await response.json()

    const animesTV = data.anime.filter(val => val.type === 'TV')
    console.log(animesTV)
    return { animesTV }
  }*/

  constructor(props) {
    super(props);
    this.state = {
    	animesTV: [],
    	info: false
    };
    this.handleInfo = this.handleInfo.bind(this)
  }
  async componentDidMount() {
    const response = await fetch(`https://api.jikan.moe/v3/season/${(new Date()).getYear() + 1900}/${getSeason()}`)
    const data = await response.json()

    const animesTV = data.anime.filter(val => val.type === 'TV')
    this.setState({ animesTV })
  }

  handleInfo(info){
  	this.setState({ info })
  }

  render() {
    return (
      <Layout>
        <div id="grid">
          { this.state.info ? 
          	<div id="info">

          	</div> :
          	null
          }
          {
          	this.state.animesTV.map((val, index) => {
          		return (
          			<Cadre info={this.handleInfo} anime={val} key={val.mal_id} />
          		)
          	})
          }
          <style jsx>{`
            #grid {
            	display: flex;
            	flex-wrap: wrap;
            	justify-content: space-around
            	position: relative;
            }
            #info {
            	position: absolute;
            	top: 0;
            	left: 0;
            	width: 100%;
            	height: 100%;
            	background-color: #ffffff1f;
            }
          `}</style>
        </div>
      </Layout>
    )
  }
}

function getSeason() {
    const month = new Date().getMonth() + 1
    let season = '';
    switch(month) {
        case 12:
        case 1:
        case 2:
            season = 'winter';
        break;
        case 3:
        case 4:
        case 5:
            season = 'spring';
        break;
        case 6:
        case 7:
        case 8:
            season = 'summer';
        break;
        case 9:
        case 10: 
        case 11:
            season = 'fall';
        break;
    }
    return season
}